.current-user {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: #3E3F3A;
  height: 50px;
  display: flex;
  align-items: center; }
  .current-user--name {
    color: #fff;
    font-weight: 700;
    padding: 10px 20px; }
  .current-user--actions {
    margin-left: auto;
    display: flex;
    height: 100%; }
    .current-user--actions__action {
      height: 100%;
      display: block;
      border-left: 1px solid #212529;
      color: rgba(255, 255, 255, 0.5);
      transition: all .2s;
      width: 30px;
      text-align: center;
      line-height: 50px; }
      .current-user--actions__action:hover {
        color: white; }
    .current-user--actions__notifications {
      display: block;
      border: 0;
      border-left: 1px solid #212529;
      background-color: #F8F5F0;
      color: #3E3F3A;
      font-size: 16px;
      position: relative; }
      .current-user--actions__notifications:hover {
        color: rgba(62, 63, 58, 0.5); }
      .current-user--actions__notifications--number {
        display: block;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background-color: #212529;
        color: #fff;
        position: absolute;
        top: 10px;
        left: 50%;
        transform: translateX(-50%);
        font-size: 8px;
        line-height: 10px;
        font-weight: 700; }
