.side-nav {
  position: fixed;
  width: 220px;
  left: 0;
  top: 0;
  bottom: 0; }
  .side-nav .site-logo {
    display: block;
    width: 100%;
    max-height: 70px;
    background-color: #3E3F3A;
    padding: 10px; }
    .side-nav .site-logo img {
      display: block;
      width: 100%;
      height: 50px;
      object-fit: contain; }
  .side-nav .site-nav {
    background-color: #8E8C84;
    position: absolute;
    top: 108px;
    bottom: 50px;
    width: 100%;
    overflow-y: auto; }
    .side-nav .site-nav--list {
      padding: 10px 0; }
      .side-nav .site-nav--list--item a {
        display: block;
        color: rgba(255, 255, 255, 0.5);
        padding: 10px 20px;
        transition: all .2s; }
        .side-nav .site-nav--list--item a i {
          margin-right: 20px; }
        .side-nav .site-nav--list--item a:hover {
          color: white; }
